.search_bar_container{
    position: relative;
    display: flex;
    height: 5rem;
    margin: auto auto auto 2.4rem;
}
.search_bar_container_form{
    display: flex;
    width: 100%;
    height: 100%;
    background-color: #fff;
    border: 1px solid #BDBDBD;
    filter: drop-shadow(0px 1px 6px rgba(0, 0, 0, 0.1));
    border-radius: 1.2rem;
    z-index: 2;
}

.search_bar_container_form_btn{
    background: none;
    border: none;
    margin: auto 0;
    padding: 0 2rem;
    cursor: pointer;
}

.search_bar_container_form_input{
    display: inline-block;
    width: 100%;
    height: 100%;
    background: none;
    outline: none;
    border: none;
}
.search_bar_dropdown_search{
    position: absolute;
    left: 0;
    top: 0;
    padding-top: 4rem;
    width: 100%;
    background-color: #fff;
    border: 1px solid #BDBDBD;
    filter: drop-shadow(0px 1px 6px rgba(0, 0, 0, 0.1));
    border-radius: 1.2rem;
    list-style: none;
    z-index: 1;
}
.search_bar_dropdown_search>li{
    width: 100%;
    height: 4rem;
}
.search_bar_dropdown_search button{
    width: 100%;
    height: 100%;
    background: none;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: #BDBDBD;
    border: none;
    cursor: pointer;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.search_bar_dropdown_search button:hover{
    background-color: #bdbdbd31;
}

.search_bar_dropdown_search li:first-child{
    height: 5rem;

}
.search_bar_dropdown_search li:first-child>button{
    padding-top: 2rem;
}
.search_bar_dropdown_search li:last-child>button{
    border-radius: 0 0 1.2rem 1.2rem;
}

@media screen and (max-width:600px) {
    .search_bar_dropdown_search, .search_bar_container_form{
        width: 80%;
    }
  }